// OSS 静态资源配置
// 只需要在这里修改 OSS 地址即可

// OSS 基础地址（CDN 地址）
// 根据你使用的 OSS 服务商填写：
// 阿里云 OSS: https://your-bucket.oss-cn-hangzhou.aliyuncs.com
// 腾讯云 COS: https://your-bucket.cos.ap-guangzhou.myqcloud.com
// 七牛云: https://your-bucket.qiniucdn.com
// 又拍云: https://your-bucket.b0.upaiyun.com
// 其他: https://your-cdn-domain.com

export const OSS_BASE_URL = 'https://your-oss-domain.com'

// 图片路径前缀（可选，如果图片直接放在根目录可以留空）
export const OSS_IMAGE_PREFIX = '/images'

// 获取完整的图片 URL
export function getImageUrl(path) {
  // 如果已经是完整 URL，直接返回
  if (path && (path.startsWith('http://') || path.startsWith('https://'))) {
    return path
  }
  
  // 如果是本地路径（/static/），保持原样（tabBar 图标必须用本地）
  if (path && path.startsWith('/static/')) {
    return path
  }
  
  // 拼接 OSS 地址
  const cleanPath = path ? path.replace(/^\//, '') : ''
  return `${OSS_BASE_URL}${OSS_IMAGE_PREFIX}/${cleanPath}`
}

// 获取 Banner 图片 URL
export function getBannerUrl(filename) {
  return getImageUrl(`banner/${filename}`)
}

// 获取商品图片 URL
export function getGoodsImageUrl(path) {
  return getImageUrl(`goods/${path}`)
}

// 获取用户头像 URL
export function getAvatarUrl(path) {
  return getImageUrl(`avatar/${path}`)
}

